.formbg {
    margin: 0px auto;
    width: 100%;
    max-width: 490px;
    border-radius: 10px;
    font-weight: 600;
}

.padding-horizontal--48 {
    padding: 48px;
}

.padding-bottom--15 {
    padding-bottom: 15px;
}

.padding-bottom--24 {
    padding-bottom: 24px;
}

.padding-bottom--24 label {
    font-weight: 600;
    padding-bottom: 10px;
}

.padding-bottom--24 input {
    font-size: 16px;
    line-height: 28px;
    padding: 8px 16px;
    width: 100%;
    min-height: 44px;
    border: unset;
    outline-color: rgba(212, 148, 84, 0.5);
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(60, 66, 87, 0.16) 0px 0px 0px 1px, rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px;
}

.chechbox_styling {
    display: flex;
    align-items: center;
    padding-bottom: 15px;
    flex-basis: 10%;
}

.chechbox_styling label {
    flex-basis: 60%;
    display: flex;
    align-items: center;
    margin: 0;
}

.chechbox_styling input {
    width: 20px;
    height: 15px;
    margin-right: 5px;
    box-shadow: unset;
    min-height: unset;
}

.field a {
    text-decoration: none;
    color: rgb(84, 105, 212);
}

.loginHeader {
    font: inherit;
    padding: 0.5rem;
    color: rgb(0, 0, 0);
    cursor: pointer;
    width: 50%;
    display: block;
    margin: 0 auto;
    transition: all 0.3s ease;
    border-radius: 4px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 10px;
    padding: 1rem 0rem;
    font-size: 23px;
}

.loginHeader:hover {
    background-color: rgb(3, 18, 94);
}